.container {
  display: flex;
  flex-direction: column;
  height: 700px;
  width: 350px;
  margin: 5px auto;
  /* justify-content: center; */
  align-items: center;
  background-color: rgb(40, 2, 57);
}
.input {
  height: 60px;
  width: 140px;
  font-size: 16px;
  margin: 100px;
  text-align: center;
  border-radius: 10px;
  border: 2px solid rgb(71, 2, 94);
}
.buttonContainer {
  display: flex;
  width: 100%;
  justify-content: space-around;
  /* border: 1px solid black; */
  margin: 20px;
}
.white {
  color: white;
  text-decoration: none;
}
button {
  background-color: aquamarine;
  height: 50px;
  width: 45%;
  text-align: center;
  color: white;
  font-size: 20px;
  font-weight: 600;
  border: none;
  border-radius: 10px;
}
