.containerBalance {
  height: 700px;
  width: 350px;
  /* border: 2px solid black; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: rgb(40, 2, 57);
  margin: 5px auto;
  padding-bottom: 100px;
}
.mainBalance {
  font-size: 40px;
  margin: 0;
  padding: 0;
}
.balanceShow {
  height: 70px;
  /* border: 1px solid white; */
}
.smallp {
  font-size: 10px;
  color: white;
  margin-bottom: 5px;
}
.imageTick {
  height: 240px;
  width: 240px;
  border-radius: 50%;
}
.displayFlex {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  /* border: 2px solid white; */
}
.middleContainer {
  height: 260px;
  /* border: 2px solid wheat; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
}
.adv {
  height: 300px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* border: 2px solid wheat; */
}
.linksHeader {
  margin-left: 12px;
}
.advContainer {
  display: flex;
  height: 50%;
  width: 100%;
  /* border: 2px solid white; */
  justify-content: space-between;
}
.smallImg {
  height: 20px;
  width: 60px;
  border-radius: 2px;
}
.heading {
  padding: 0px 60px;
}
.links {
  height: 100%;
  width: 23%;
  /* border: 2px solid white; */
}
.linksImg {
  height: 40px;
  width: 40px;
  border-radius: 10px;
}
.linksPara {
  color: white;
  font-size: 10px;
}
.sipLink {
  width: 96%;
  background-color: rgba(86, 36, 110, 0.237);
  border-radius: 15px;
  /* border: 2px solid white; */
  text-align: start;
  margin: 10px auto;
  font-size: 15px;
  height: 45px;
  padding: 10px;
}
.sipimg {
  height: 40px;
  width: 40px;
}
.lastbutton {
  margin-top: 20px;
}
/* //common css */
.white {
  color: white;
  text-decoration: none;
}
